/* ====== Layout base ====== */
.booking-list-page {
  font-family: 'Inter', 'Segoe UI', 'Helvetica Neue', Arial, sans-serif;
  padding: 20px 18px 40px;
  color: #1f2937;
  background: #f5f7fb;
  min-height: 100%;
  box-sizing: border-box;
}

.blp-header {
  display: flex;
  align-items: center;
  gap: 16px;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 4px 0 12px;
}

.blp-title {
  font-size: 26px;
  font-weight: 800;
  letter-spacing: .01em;
  color: #0f172a;
  margin: 0;
}

/* pill filter */
.blp-filter {
  display: flex;
  align-items: center;
  gap: 8px;
}
.blp-filter-label {
  color: #6b7280;
  font-size: 14px;
}
.blp-chip {
  border: 1px solid #e5e7eb;
  background: #fff;
  color: #374151;
  padding: 6px 12px;
  border-radius: 999px;
  font-size: 13px;
  cursor: pointer;
  transition: background .15s ease, box-shadow .15s ease, border-color .15s ease, transform .03s ease;
}
.blp-chip:hover { background: #f7f7fb; }
.blp-chip.is-active {
  background: #eef2ff;
  border-color: #c7d2fe;
  color: #4338ca;
  box-shadow: 0 4px 10px rgba(79,70,229,.12);
}

.blp-count {
  color: #6b7280;
  font-size: 14px;
  margin: 4px 2px 14px;
}

.blp-state,
.blp-empty {
  color: #6b7280;
  font-size: 14px;
  text-align: center;
  padding: 24px 0;
}
.blp-error {
  color: #b91c1c;
  background: #fee2e2;
  border: 1px solid #fecaca;
  border-radius: 12px;
  padding: 12px 14px;
  margin: 8px 0 14px;
}

/* ====== list ====== */
.plan-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.plan-row {
  background: #fff;
  border: 1px solid #e5e7eb;
  border-radius: 14px;
  box-shadow: 0 6px 16px rgba(15,23,42,.06);
  padding: 12px 14px;
}
.plan-row.is-deleted {
  opacity: .7;
}

.plan-main {
  display: flex;
  align-items: center;
  gap: 14px;
}

.plan-badge {
  width: 38px;
  height: 38px;
  border-radius: 50%;
  background: #eef2ff;
  color: #4338ca;
  display: grid;
  place-items: center;
  font-weight: 800;
  letter-spacing: .02em;
  flex: 0 0 auto;
}

.plan-texts {
  min-width: 0;
  flex: 1 1 auto;
}

.plan-title {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 800;
  color: #111827;
  font-size: 16px;
  line-height: 1.2;
  margin-bottom: 4px;
}

.plan-sub {
  color: #4b5563;
  font-size: 14px;
  line-height: 1.4;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* status tags */
.tag {
  display: inline-flex;
  align-items: center;
  border-radius: 999px;
  padding: 2px 8px;
  font-size: 12px;
  font-weight: 700;
  border: 1px solid #e5e7eb;
  background: #fff;
  color: #374151;
}
.tag.ok {
  background: #ecfdf5;
  border-color: #bbf7d0;
  color: #065f46;
}
.tag.muted {
  background: #f3f4f6;
  color: #6b7280;
}
.tag.danger {
  background: #fee2e2;
  border-color: #fecaca;
  color: #991b1b;
}

.plan-actions {
  display: flex;
  gap: 8px;
  align-items: center;
}

/* buttons */
.btn {
  height: 36px;
  padding: 0 14px;
  border-radius: 10px;
  font-weight: 700;
  font-size: 14px;
  cursor: pointer;
  border: 1px solid #e5e7eb;
  background: #fff;
  color: #374151;
  transition: background .15s ease, border-color .15s ease, box-shadow .15s ease, transform .03s ease;
}
.btn:hover { background: #f3f4f6; }
.btn:active { transform: translateY(1px); }
.btn.ghost {
  box-shadow: 0 8px 18px rgba(0,0,0,.06);
}

/* pager */
.blp-pager {
  display: flex;
  justify-content: center;
  gap: 10px;
  align-items: center;
  margin: 18px 0 6px;
}
.pager-btn {
  height: 36px;
  padding: 0 14px;
  border-radius: 999px;
  background: #fff;
  border: 1px solid #e5e7eb;
  color: #374151;
  cursor: pointer;
  transition: background .15s ease, border-color .15s ease, transform .03s ease;
}
.pager-btn:hover { background: #f7f7fb; }
.pager-btn:disabled {
  opacity: .45;
  cursor: not-allowed;
}
.pager-info {
  font-size: 14px;
  color: #6b7280;
}

/* responsive */
@media (max-width: 640px) {
  .plan-main { align-items: flex-start; }
  .plan-actions { margin-left: auto; }
  .plan-sub { white-space: normal; }
}